<template>
    <section  class="section" style="padding: 1rem ; min-height: 600px;">
        <div class="">
            <div class="columns">
                <div class="column is-8 is-offset-2">
                                        <horizontal-stepper id="step" @completed-step="completeStep" @active-step="isStepActive" @stepper-finished="finish" :steps="steps">
                    </horizontal-stepper>
                </div>
            </div>
        </div>
    </section>
</template>



<script>
import HorizontalStepper from 'vue-stepper';
import HelloWorld from './HelloWorld.vue';
import dns from './poo.vue';
export default {
    name: 'StepperMain',
    components: {
        HorizontalStepper,

    },
    data() {
        return {
            steps: [
                {
                    icon: 'link',
                    name: 'first',
                    title: 'Link Origin With DID',
                    component: HelloWorld,
                    completed: false
                }, {
                    icon: 'check',
                    name: 'second',
                    title: 'Prove Ownership',
                    component: dns,
                    completed: false
                }

            ]

        }
    },
    methods: {
        finish() {
            
        },
        completeStep(payload) {
            this.steps.forEach((step) => {

                if (step.name === payload.name) {
                    step.completed = true;
                }
            })
        },
        // Executed when @active-step event is triggered
        isStepActive(payload) {
            this.steps.forEach((step) => {
                if (step.name === payload.name) {
                    if (step.completed === true) {
                        step.completed = false;
                    }
                }
            })
        },
    }

}
</script>


