    padding: 3rem 1.5rem;
    margin: 0 auto;
    max-width: 960px;
    width: 100%;
    box-sizing: border-box;
    position: relative;
    z-index: 1;

}

#step{
    border-radius: 10px;
    box-shadow: #68b9a9f5 0px 1px 4px;
}


