const { DirectSecp256k1HdWallet } = require("@cosmjs/proto-signing");
const {   Bip39, Slip10, Slip10RawIndex, Slip10Curve } = require('@cosmjs/crypto')

// const mnemonic = "retreat seek south invite fall eager engage endorse inquiry sample salad evidence express actor hidden fence anchor crowd two now convince convince park bag"
// const mnemonic = "napkin delay purchase easily camp mimic share wait stereo reflect allow soccer believe exhibit laptop upset tired talent transfer talk surface solution omit crack"

function makeCosmoshubPath(a) {
    return [
        Slip10RawIndex.hardened(44),
        Slip10RawIndex.hardened(118),
        Slip10RawIndex.hardened(0),
        Slip10RawIndex.normal(0),
        Slip10RawIndex.normal(a),

    ];
}
const createWallet = async (mnemonic, id=0) => {
    if (!mnemonic) {
        return await DirectSecp256k1HdWallet.generate(24,  {
            prefix: "hid",
            hdPaths: [makeCosmoshubPath(id)],
        });
    } else {
        return await DirectSecp256k1HdWallet.fromMnemonic(mnemonic,  {
            prefix: "hid",
            hdPaths: [makeCosmoshubPath(id)],
        });
    }
}
function makeSSIWalletPath(minHardIndex) {
    return [
       
        Slip10RawIndex.hardened(118),
        Slip10RawIndex.hardened(128),
        Slip10RawIndex.hardened(0),
        Slip10RawIndex.hardened(minHardIndex),

    ];
}

 async function generateMnemonicToHDSeed(mnemonic, minHardIndex = 0) {
    minHardIndex=minHardIndex+1;
    const seed = Bip39.decode(mnemonic)
    const slipPathKeys = Slip10.derivePath(Slip10Curve.Ed25519, seed, makeSSIWalletPath(minHardIndex))
    const seedHD = slipPathKeys.privkey
    return seedHD;
}

module.exports = {
    createWallet,
    generateMnemonicToHDSeed
}