<template>
    <div class="form">
        <!-- <p id="header">DNS-01</p> -->
        <h5 style="color:red" v-if="domain">Please add this TXT record to your domain for DNS-01 validation.</h5>
        <span id="copy">
            <a href="#" id="copytxt" @click="copy">Copy</a>
        </span>
        
        <label for="basic-url">TXT Record</label>
        <input type="text" id="txt" :value="txtRecord" disabled/>
        <br>
        <button id="verify" @click="verify">Verify</button>
        <br>
        <div id="status" v-if="status">
            <p style="font-size: small;">
                <b>Origin:</b> {{ domain }}
                <br>
                <b>TXT Record:</b> {{ txtRecord }}
                <br>
                <b>Status :</b> {{ status }}
                <br>
                <b id="flash-text"><a @click="openURl(didDocument.id)" style="text-decoration:none ; cursor: pointer; ">DID: {{ didDocument.id }} </a>
                </b>
                <br>

            </p>
        </div>



        <!-- copy span -->



    </div>
</template>
 

<script>
import DomainLinkage from '@hypersign-protocol/domain-linkage-verifier'
import { registerDID } from '../scripts/DidOperations'
import {
    HypersignDID
} from 'hs-ssi-sdk'
export default {
    name: 'PooDns',
    data() {
        return {
            txtRecord: '',
            domain: '',
            didDocument: '',
            status: false,
            rpc: 'https://rpc.jagrat.hypersign.id/',
            api: 'https://api.jagrat.hypersign.id/'
        }
    },
    mounted() {
        document.getElementById("verify").style.display = "block";
        this.domain = localStorage.getItem('domain')
        this.didDocument = JSON.parse(localStorage.getItem('didDocument'))

        this.txtRecord = "hypersign-domain-verification.did=" + JSON.parse(localStorage.getItem('didDocument')).id
    },
    methods: {
        async signAndRegisterDid() {
            const key = JSON.parse(localStorage.getItem('keys'))
            const hsDID = new HypersignDID({
                namespace: 'testnet',
                nodeRestEndpoint: this.api,
                nodeRpcEndpoint: this.rpc
            });

            const signature = await hsDID.signDid({ didDocument: this.didDocument, privateKeyMultibase: key.privateKeyMultibase })
            const data = await registerDID(this.didDocument, signature)
            return data;
        },
        openURl(url) {
            window.open('https://explorer.hypersign.id/hypersign-testnet/identity/' + url, '_blank');
        },
        copy() {
            var copyText = document.getElementById("txt");
            copyText.select();
            copyText.setSelectionRange(0, 99999)
            // document.execCommand("copy");
            document.getElementById("copytxt").innerHTML = "Copied!";
            // color fade 
            document.getElementById("copytxt").style.color = "#90EE90";
            this.alert('Copied to clipboard')
        },

        alert(msg, time = 3000) {
            //  a alert that will push a floatable div in the left corner
            const div = document.createElement('div');
            div.className = 'alert';
            // style for top right corner floatable div fade in and out transperent blue
            div.style.cssText = 'position:fixed; top:10px; right:10px; background-color: #054126; color: #fff; padding: 10px; border-radius: 5px; z-index: 9999; animation: fadein 0.5s, fadeout 0.5s 2.5s;';
            div.innerHTML = msg;
            document.body.appendChild(div);
            setTimeout(function () {
                div.remove();
            }, time);
        },
        async verify() {
            try {


                this.loader(true)
                const domainLinkage = new DomainLinkage(this.domain)
                const txtRecord = await domainLinkage.verifyDnsTxtRecord(this.domain.includes('http') ? this.domain : new URL('https://' + this.domain), this.txtRecord)
                const resp = await this.signAndRegisterDid()
                console.log(resp.data);

                if (txtRecord) {
                    this.alert('Domain verified successfully')
                    this.$emit('can-continue', {
                        value: true
                    })
                    document.getElementById("verify").innerHTML = "DNS-01 Verified ✔ ";
                    document.getElementById("verify").style['background-color'] = "#28A745"; this.status = true
                    const resp = await this.signAndRegisterDid()
                    if (resp.data) {
                        document.getElementById("status").style.color = "#28A745";
                        document.getElementById("status").style.border = "1px solid #28A745";
                        this.alert(resp.data.transactionHash)

                    } else {
                        this.alert("DID not registred")
                        document.getElementById("status").style.color = "#DC3545";
                        document.getElementById("status").style.border = "1px solid #DC3545";
                        // Arry to string
                        const errorMsgs = resp.message.join(', ')
                        if (errorMsgs.includes('already exists')) {
                            this.alert("DID already registred")
                            document.getElementById("status").style.color = "#28a745";
                            document.getElementById("status").style.border = "1px solid #28a745";
                        }
                        // qury selector for flash-text id

                    }

                    // await this.register()
                } else {
                    this.alert('Domain verification failed')
                    // retry 
                    document.getElementById("verify").innerHTML = "Retry ↻ ";
                    document.getElementById("verify").style['background-color'] = "#DC3545";


                }

                this.loader(false)
            } catch (error) {
                console.log(error);

                this.loader(false)
                this.alert('Domain verification failed')
                document.getElementById("verify").innerHTML = "Retry ↻ ";
                document.getElementById("verify").style['background-color'] = "#DC3545";

            }
        },
        // async register() {
        //     const hsDID = new HypersignDID({
        //         namespace: 'testnet',
        //         nodeRestEndpoint: this.api,
        //         nodeRpcEndpoint: this.rpc
        //     });
        //     console.log(hsDID.sig);
        // },


        loader(show = true) {
            // a div based loader that will show a loader in the middle of the screen based on show variable and transparent background and animated and use the loader image from cdn 
            if (show) {
                const div = document.createElement('div');
                div.className = 'loader';
                div.style.cssText = 'position:fixed; top:0; left:0; width:100%; height:100%; background-color: rgba(0,0,0,0.8); z-index: 9999; display:flex; justify-content:center; align-items:center;';
                div.img = document.createElement('img');
                div.img.style.cssText = 'width:150px'
                div.img.src = 'https://cdn.dribbble.com/users/421466/screenshots/2377950/drops-400px.gif'

                div.appendChild(div.img)

                document.body.appendChild(div);
            } else {
                document.querySelector('.loader').remove()
            }


        }
    }
}

</script>


