    upload() {
      try {


        const file = this.$refs.file.files[0]
        const reader = new FileReader()
        reader.onload = (e) => {

          const data = JSON.parse(e.target.result)
          localStorage.setItem('didDocument', JSON.stringify(data.didDocument))
          localStorage.setItem('didConfiguration', JSON.stringify(data.didConfiguration))
          localStorage.setItem('mnemonic', data.mnemonic)
          localStorage.setItem('keys', JSON.stringify(data.keys))
          localStorage.setItem('domain', new URL(data.didDocument.service[0].serviceEndpoint).hostname)
          this.walletAddress = data.keys.address
          this.domain = data.didDocument.service[0].serviceEndpoint.split('/')[2]
          document.getElementById("verify").style.display = "block";
          document.getElementById("verify").disabled = false;
          document.getElementById("verify").innerHTML = "Verify";
          document.getElementById("link").style.display = "none";
          this.didDocument = data.didDocument
          this.didConfiguration = data.didConfiguration
          this.import_file = false
        }
        reader.readAsText(file)
      } catch (error) {
        console.log(error);
        this.alert('error', 'Something went wrong')

      }

    },
    import_config() {
      this.import_file ? this.import_file = false : this.import_file = true



    },
    export_config() {
      const didDocument = JSON.parse(localStorage.getItem('didDocument'))
      const didConfiguration = JSON.parse(localStorage.getItem('didConfiguration'))
      const mnemonic = localStorage.getItem('mnemonic')
      const keys = JSON.parse(localStorage.getItem('keys'))
      const data = {
        didDocument,
        didConfiguration,
        mnemonic,
        keys
      }
      this.download(data, 'exportconfig.json')


    },
    async resolveDID() {
      const resolveDID = 'https://' + this.domain + '/.well-known/did.json'
      const resolveDIDConfig = 'https://' + this.domain + '/.well-known/did-configuration.json'

      const didDocument = await this.fetch(resolveDID)
      const didConfiguration = await this.fetch(resolveDIDConfig)
      if (didDocument.id != JSON.parse(localStorage.getItem('didDocument')).id) {
        this.alert('Domain is not linked to the DID')
        document.getElementById("verify").innerHTML = "Retry ↻  ";

        return
      }

      const credential = didConfiguration.linked_dids[0]
      if (credential.credentialSubject.id != didDocument.id) {
        this.alert('Domain is not linked to the DID')
        document.getElementById("verify").innerHTML = "Retry ↻  ";

        return
      }
      if (credential.credentialSubject.id != credential.credentialSubject.id) {
        this.alert('DidConfiguration is not linked to the DID')
        document.getElementById("verify").innerHTML = "Retry ↻  ";

        return
      }

      if (credential.credentialSubject.origin != this.domain) {
        this.alert('Domain is not linked to the DID')
        document.getElementById("verify").innerHTML = "Retry ↻  ";

        return
      }
    },
    download(file, filename) {
      const blob = new Blob([JSON.stringify(file)], { type: 'application/json' });
      const url = URL.createObjectURL(blob);
      const link = document.createElement('a');
      link.href = url;
      link.download = filename;
      // attach this link to the document a of id did
      link.click();

    },

