      // a div based loader that will show a loader in the middle of the screen based on show variable and transparent background and animated and use the loader image from cdn 
      if (show) {
        const div = document.createElement('div');
        div.className = 'loader';
        div.style.cssText = 'position:fixed; top:0; left:0; width:100%; height:100%; background-color: rgba(0,0,0,0.8); z-index: 9999; display:flex; justify-content:center; align-items:center;';
        div.img = document.createElement('img');
        div.img.style.cssText = 'width:150px'
        div.img.src = 'https://cdn.dribbble.com/users/421466/screenshots/2377950/drops-400px.gif'

        div.appendChild(div.img)

        document.body.appendChild(div);
      } else {
        document.querySelector('.loader').remove()
      }



    },
    alert(msg, time = 3000) {
      //  a alert that will push a floatable div in the left corner
      const div = document.createElement('div');
      div.className = 'alert';
      // style for top right corner floatable div fade in and out transperent blue
      div.style.cssText = 'position:fixed; top:10px; right:10px; background-color: #054126; color: #fff; padding: 10px; border-radius: 5px; z-index: 9999; animation: fadein 0.5s, fadeout 0.5s 2.5s;';
      div.innerHTML = msg;
      document.body.appendChild(div);
      setTimeout(function () {
        div.remove();
      }, time);




    },
    copy(text) {
      // copy to clipboard
      const that = this
      navigator.clipboard.writeText(text).then(function () {
        that.alert('Copied to clipboard')
      }, function (err) {
