      const didConfigurationBlob = new Blob([JSON.stringify(didConfiguration)], { type: 'application/json' });
      const didDocumentUrl = URL.createObjectURL(didDocumentBlob);
      const didConfigurationUrl = URL.createObjectURL(didConfigurationBlob);
      const didDocumentLink = document.createElement('a');
      const didConfigurationLink = document.createElement('a');
      didDocumentLink.href = didDocumentUrl;
      didConfigurationLink.href = didConfigurationUrl;
      didDocumentLink.download = 'did.json';
      didConfigurationLink.download = 'did-configuration.json';
      // attach this link to the document a of id did
      didDocumentLink.click();
      didConfigurationLink.click();

      document.getElementById("verify").style.display = "block";
      document.getElementById("verify").disabled = false;
      document.getElementById("verify").innerHTML = "Verify";
      document.getElementById("link").style.display = "none";

      this.loader(false)


    },
