    async Verify() {
      try {
        this.loader(true)
        const dl = new DomainLinkage(this.domain)
        var button = document.getElementById("verify");
        button.innerHTML = "Processing";
        button.disabled = true;
        const result = await dl.verify(this.didDocument.id)

        button.style['background-color'] = "#e4a11b";
        if (result.verified) {
          console.log(result);
          this.alert('Domain is linked to the DID.\nProceed to the next step')

          // button.disabled = true;

          button.innerHTML = "Verified ✔";
          button.style['background-color'] = "#14A44D";
          button.disabled = true;


          this.loader(false)
          return this.$emit('can-continue', { value: true });

        }
        else {
          this.alert('Domain is not linked to the DID')
          button.innerHTML = "Retry ↻  ";
          button.style['background-color'] = "#DC4C64";
          button.disabled = false;
          this.loader(false)
        }

      }
      catch (e) {
        console.log(e);
     

        this.alert('Domain is not linked to the DID')
        document.getElementById("verify").innerHTML = "Retry ↻  ";
        button.style['background-color'] = "#DC4C64";

        document.getElementById("verify").disabled = false;
        this.loader(false)
      }

    },

